.meal {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}

.meal h3 {
  margin: 0 0 0.5rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: var(--primary-dark);
  font-size: 1.25rem;
}

@media (max-width: 600px) {
  .meal {
  flex-wrap: wrap;
  }
  .price{
    text-align: right;
  }
  .meal >div:last-child {
    flex: 0 0 100%;
    margin-top: 1rem;
  }
}