.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: var(--primary);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}
@media (max-width: 500px) {
  .header {
    padding: 0 10px;
  }
  .header h2 span {
    font-size:0;
  }
  .header h2 {
    font-size: 3rem;
    margin: 0 0 5px 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

}